import React, {Component} from "react";
import Grid from "../Grid";
import HUD from "../HUD";

class App extends Component {
  state = {
    mineAmount: 10,
    flagCount: 0,
    cols: 20,
    rows: 20,
  };

  handleFlagCount = (flag) => {
    const {flagCount} = this.state;
    let newFlagCount = flagCount;
    if (!flag) {
      newFlagCount++;
    } else {
      newFlagCount--;
    }
    this.setState({flagCount: newFlagCount});
  };

  render() {
    const {cols, rows, flagCount, mineAmount} = this.state;

    return (
      <>
        <HUD mineAmount={mineAmount} flagCount={flagCount} />
        <Grid
          flagCount={flagCount}
          handleFlagCount={this.handleFlagCount}
          cols={cols}
          rows={rows}
          mineAmount={mineAmount}
        />
      </>
    );
  }
}

export default App;
